<template>
  <div class="searchresults">
    <div class="container-fluid content">
      <h4>查詢結果</h4>
      <hr>
        <div class="row d-flex justify-content-center">
          <div class="result-list col-8" v-bind:key="place.json_d" v-for="place in places.slice(0,8)">
            <router-link :to="{path: '/placedetail/'+ place.json_d}">
            <div class="row">
              <div class="col-4">
                <img :src="place.picture1" img-alt="Image" style="max-width: 30vh;max-height: 20vh;">
              </div>
              <div class="col-8">
                <h3 class="text-content">{{ place.name }}</h3>
                <p class="text-content">{{ place.toldescribe|truncate(150, '...') }}</p>
              </div>
            </div>
            </router-link>
          </div>
        </div>
    </div>
  </div>  
</template>

<script>
export default {
    name: 'SearchResults',
    watch: {
        "$route.params.value": {
            handler() {
                this.getPlaces();
            },
        immediate: true
        }
    },
    data(){
        return{
            places : [],
        }
    },
    methods:{
        getPlaces(){
            this.$ajax.get(process.env.VUE_APP_BACKEND_SERVER+"/api/trips/places/?name="+ this.$route.params.value)
            .then(res => (
                this.places = res.data
            ))
            // .then(res => (console.log(res.data)))
            .catch(err => console.log(err));
        }
    },
    computed:{
    }
}
</script>
<style scoped>

.content{
  padding: 1vw;
}

.result-list{
  padding: 1vw;
}

.text-content, a:link, a:visited {
  color:#000000;
  text-decoration:none;
  text-align: left;
}


h4 {
    padding-top: 2vh;
}

</style>